import React from "react";
import Apartment from "./Apartment";
import ApartmentData from "./Apartments.json";

function Apartments({ showAll, searchValue }) {
  return (
    <div>
      <div>
        {ApartmentData.map(
          apart =>
            (!showAll || apart.state) &&
            (apart.title.toLowerCase().includes(searchValue) ||
              apart.price.toLowerCase().includes(searchValue)) && (
                <Apartment
                  id={apart.id}
                  title={apart.title}
                  price={apart.price}
                  rating={apart.rating}
                  imageSrc={apart.imageSrc}
                  isAvailable={apart.state}
                  isFavorite={apart.isFavorite}
                />
            )
        )}
      </div>
    </div>
  );
}

export default Apartments;
