import React, {useState} from "react";

function Apartment(props) {
  const { title, imageSrc, price, rating, isAvailable, isFavorite } = props;
  const fillColor = isAvailable ? "green" : "red";
  const [isFav, setIsFav] = useState(isFavorite);
  return (
    <div>
      <div>
        <span role="img" aria-label="">
          <img
            src={imageSrc}
            alt="apartment"
            style={{ width: "50px", height: "50px", borderRadius: "50%" }}
          />
        </span>
      </div>
      <div>
        <span>
          <svg width="10" height="10">
            <circle fill={fillColor} cx="5" cy="5" r="5" />
          </svg>
        </span>
        {title}
      </div>
      <div>{price}</div>
      <div>
        <div>
          {Array.from({ length: rating }).map(star => (
            <span role="img" aria-label="">
              ‚≠ê
            </span>
          ))}
        </div>
      </div>
      <div>
        {isFav && (
          <span role="img" aria-label="">
            üíô
          </span>
        )}
      </div>
      <button onClick={() => setIsFav(!isFav)}>
        {isFav ? "Remove favorite" : "Set favorite"}
      </button>
    </div>
  );
}

export default Apartment;
