import React, { useState } from "react";
import ApartmentsList from "./ApartmentsList";
import ApartmentData from "./Apartments.json";
import SearchBar from "./SearchBar";

function isFalse() {
  return false;
}

function App() {
  const [showAll, setShowAll] = useState(false);
  const [searchValue, setSearchValue] = useState("");
  const isMatch = ApartmentData.map(
    apt =>
    apt.state && 
      (apt.title.toLowerCase().includes(searchValue) ||
      apt.price.toLowerCase().includes(searchValue))
  );

  const isVisible = ApartmentData.map(
    apt =>
    (!showAll || apt.state) &&
      (apt.title.toLowerCase().includes(searchValue) ||
      apt.price.toLowerCase().includes(searchValue))
  ).filter(a => a === true);

  return (
    <div>
      <div>
        <div>
          <SearchBar
            searchValue={searchValue}
            setSearchValue={setSearchValue}
          />
          <button onClick={() => setShowAll(!showAll)}>
            {!showAll ? "Show only available" : "Show all"}
          </button>
          <div>
            {searchValue && isMatch.every(isFalse)
              ? `The term "${searchValue}" returned no apartment :(`
              : "Found " +
                isVisible.length +
                " out of " +
                ApartmentData.length}
          </div>
        </div>
        <ApartmentsList
          showAll={showAll}
          searchValue={searchValue.toLowerCase()}
        />
      </div>
    </div>
  );
}

export default App;